import Vue from 'vue'
import Router from 'vue-router'
import top from '@/components/top'
import designs from '@/components/designs'
import cordings from '@/components/cordings'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      component: top
    },
    {
      path: '/designs',
      component: designs
    },
    {
      path: '/cordings',
      component: cordings
    }
  ]
})


